import {
  THEME_NAME,
  getBalloonClasses,
  isSameBalloon,
  isBalloonExist,
} from '../../helpers';
import { __ } from '@wordpress/i18n';
import {
  InspectorControls,
  InnerBlocks,
  RichText,
  withColors,
  PanelColorSettings,
  withFontSizes,
  MediaUpload,
  useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, SelectControl, Button } from '@wordpress/components';
import { Fragment, useEffect } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { useSelect } from '@wordpress/data';
import classnames from 'classnames';

const DEFAULT_NAME = __( '', THEME_NAME );

const defaultIconUrl = gbSettings.speechBalloonDefaultIconUrl
  ? gbSettings.speechBalloonDefaultIconUrl
  : '';

let speechBalloons = gbSpeechBalloons;

if ( ! isBalloonExist( speechBalloons ) ) {
  speechBalloons = [
    {
      name: '',
      id: '0',
      icon: defaultIconUrl,
      style: 'cb',
      position: 'l',
      iconstyle: 'stn',
      visible: '1',
    },
  ];
}

export function BalloonEdit( props ) {
  const {
    attributes,
    setAttributes,
    className,
    backgroundColor,
    setBackgroundColor,
    textColor,
    setTextColor,
    borderColor,
    setBorderColor,
    fontSize,
    clientId,
  } = props;

  let {
    name,
    index,
    id,
    icon,
    style,
    position,
    iconstyle,
    iconid,
    customBackgroundColor,
    customTextColor,
    customBorderColor,
    notNestedStyle,
    backgroundColorValue,
    textColorValue,
    borderColorValue,
  } = attributes;

  //
  if ( ! icon && index == '0' && speechBalloons.length > 0 ) {
    speechBalloons.some( ( balloon, index ) => {
      //console.log(balloon);
      if ( balloon.visible == '1' ) {
        id = balloon.id;
        icon = balloon.icon;
        style = balloon.style;
        position = balloon.position;
        iconstyle = balloon.iconstyle;
        if ( ! name ) {
          name = balloon.name;
        }
        setAttributes( {
          name,
          index,
          id,
          icon,
          style,
          position,
          iconstyle,
        } );
        return true;
      }
    } );
  }

  //
  if ( speechBalloons[ index ] ) {
    if ( isSameBalloon( index, id, icon, style, position, iconstyle ) ) {
      id = speechBalloons[ index ].id;
      icon = speechBalloons[ index ].icon;
      style = speechBalloons[ index ].style;
      position = speechBalloons[ index ].position;
      iconstyle = speechBalloons[ index ].iconstyle;
      if ( ! name ) {
        name = speechBalloons[ index ].name;
      }
      // setAttributes( { index: index, id: id, icon: icon, style: style, position: position, iconstyle: iconstyle } );
    }
  }

  const renderIcon = ( obj ) => {
    return (
      <Button
        className="image-button"
        onClick={ obj.open }
        style={ { padding: 0 } }
      >
        <img
          src={ icon ? icon : speechBalloons[ index ].icon }
          alt={ icon ? '' : speechBalloons[ index ].name }
          className={ `speech-icon-image wp-image-${ iconid }` }
        />
      </Button>
    );
  };

  const balloons = [];
  speechBalloons.map( ( balloon, index ) => {
    //console.log(balloon);
    if ( speechBalloons[ index ].visible == '1' ) {
      balloons.push( {
        value: index,
        label: balloon.title,
      } );
    }
  } );

  // notNestedStylefalse
  const isParentNestedStyle = useSelect( ( select ) => {
    const parentBlocks =
      select( 'core/block-editor' ).getBlockParents( clientId );
    for ( const parentClientId of parentBlocks ) {
      const parentBlock =
        select( 'core/block-editor' ).getBlock( parentClientId );
      if (
        parentBlock.name === props.name &&
        parentBlock.attributes.notNestedStyle === false
      ) {
        return true;
      }
    }
    return false;
  } );

  // notNestedStylefalsenotNestedStylefalse
  if ( isParentNestedStyle && notNestedStyle ) {
    setAttributes( { notNestedStyle: false } );
  }

  useEffect( () => {
    setAttributes( { backgroundColorValue: backgroundColor.color } );
    setAttributes( { textColorValue: textColor.color } );
    setAttributes( { borderColorValue: borderColor.color } );
  }, [ backgroundColor, textColor, borderColor ] );

  const classes = classnames(
    getBalloonClasses( id, style, position, iconstyle ),
    {
      'not-nested-style': notNestedStyle,
      'cocoon-block-balloon': true,
    }
  );

  const styles = {
    '--cocoon-custom-border-color': customBorderColor || undefined,
    '--cocoon-custom-background-color': customBackgroundColor || undefined,
    '--cocoon-custom-text-color': customTextColor || undefined,
  };

  if ( notNestedStyle ) {
    styles[ '--cocoon-custom-border-color' ] = borderColorValue;
    styles[ '--cocoon-custom-background-color' ] = backgroundColorValue;
    styles[ '--cocoon-custom-text-color' ] = textColorValue;
  }

  const blockProps = useBlockProps( {
    className: classes,
    style: styles,
  } );

  return (
    <Fragment>
      <InspectorControls>
        <PanelBody title={ __( '', THEME_NAME ) }>
          <SelectControl
            label={ __( '', THEME_NAME ) }
            value={ index }
            onChange={ ( value ) =>
              setAttributes( {
                index: value,
                name: speechBalloons[ value ].name,
                id: speechBalloons[ value ].id,
                icon: speechBalloons[ value ].icon,
                style: speechBalloons[ value ].style,
                position: speechBalloons[ value ].position,
                iconstyle: speechBalloons[ value ].iconstyle,
              } )
            }
            options={ balloons }
          />

          <SelectControl
            label={ __( '', THEME_NAME ) }
            value={ style }
            onChange={ ( value ) => setAttributes( { style: value } ) }
            options={ [
              {
                value: 'stn',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'flat',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'line',
                label: __( 'LINE', THEME_NAME ),
              },
              {
                value: 'think',
                label: __( '', THEME_NAME ),
              },
            ] }
          />

          <SelectControl
            label={ __( '', THEME_NAME ) }
            value={ position }
            onChange={ ( value ) => setAttributes( { position: value } ) }
            options={ [
              {
                value: 'l',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'r',
                label: __( '', THEME_NAME ),
              },
            ] }
          />

          <SelectControl
            label={ __( '', THEME_NAME ) }
            value={ iconstyle }
            onChange={ ( value ) => setAttributes( { iconstyle: value } ) }
            options={ [
              {
                value: 'sn',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'sb',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'cn',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'cb',
                label: __( '', THEME_NAME ),
              },
            ] }
          />
        </PanelBody>

        <PanelColorSettings
          title={ __( '', THEME_NAME ) }
          colorSettings={ [
            {
              label: __( '', THEME_NAME ),
              onChange: setBackgroundColor,
              value: backgroundColor.color,
            },
            {
              label: __( '', THEME_NAME ),
              onChange: setTextColor,
              value: textColor.color,
            },
            {
              label: __( '', THEME_NAME ),
              onChange: setBorderColor,
              value: borderColor.color,
            },
          ] }
          __experimentalIsRenderedInSidebar={ true }
        />
      </InspectorControls>

      <div { ...blockProps }>
        <div className="speech-person">
          <figure className="speech-icon">
            <MediaUpload
              onSelect={ ( media ) => {
                const newicon = !! media.sizes.thumbnail
                  ? media.sizes.thumbnail.url
                  : media.url;
                //console.log(newicon);
                setAttributes( {
                  icon: newicon,
                  iconid: media.id,
                } );
              } }
              type="image"
              value={ iconid }
              render={ renderIcon }
            />
          </figure>
          <div className="speech-name">
            <RichText
              value={ name }
              placeholder={ DEFAULT_NAME }
              onChange={ ( value ) => setAttributes( { name: value } ) }
            />
          </div>
        </div>
        <div
          className={ classnames( className, {
            'speech-balloon': true,
            'has-text-color': textColor.color,
            'has-background': backgroundColor.color,
            'has-border-color': borderColor.color,
            [ backgroundColor.class ]: backgroundColor.class,
            [ textColor.class ]: textColor.class,
            [ borderColor.class ]: borderColor.class,
            [ fontSize.class ]: fontSize.class,
          } ) }
        >
          <InnerBlocks placeholder={ __( '', THEME_NAME ) } />
        </div>
      </div>
    </Fragment>
  );
}

export default compose( [
  withColors( 'backgroundColor', {
    textColor: 'color',
    borderColor: 'border-color',
  } ),
  withFontSizes( 'fontSize' ),
] )( BalloonEdit );
