import { THEME_NAME } from '../../helpers';
import { __ } from '@wordpress/i18n';
import {
  InnerBlocks,
  InspectorControls,
  useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, SelectControl } from '@wordpress/components';
import { Fragment } from '@wordpress/element';
import classnames from 'classnames';

export default function edit( { attributes, setAttributes, className } ) {
  const { style } = attributes;
  const classes = classnames( 'block-box', {
    [ style ]: !! style,
    [ className ]: !! className,
  } );
  const blockProps = useBlockProps( {
    className: classes,
  } );

  return (
    <Fragment>
      <InspectorControls>
        <PanelBody title={ __( '', THEME_NAME ) }>
          <SelectControl
            label={ __( '', THEME_NAME ) }
            value={ style }
            onChange={ ( value ) => setAttributes( { style: value } ) }
            options={ [
              {
                value: 'primary-box',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'secondary-box',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'info-box',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'success-box',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'warning-box',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'danger-box',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'light-box',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'dark-box',
                label: __( '', THEME_NAME ),
              },
            ] }
          />
        </PanelBody>
      </InspectorControls>

      <div { ...blockProps }>
        <InnerBlocks />
      </div>
    </Fragment>
  );
}
