import { THEME_NAME, BLOCK_CLASS } from '../../helpers';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import classnames from 'classnames';

import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
const { RichText, InspectorControls } = wp.editor;
const { PanelBody, SelectControl, BaseControl, TextareaControl } =
  wp.components;
import { Fragment } from '@wordpress/element';
const BUTTON_BLOCK = ' button-block';

registerBlockType( 'cocoon-blocks/button-wrap', {
  title: __( '', THEME_NAME ),
  icon: 'dismiss',
  category: THEME_NAME + '-old',
  description: __(
    '',
    THEME_NAME
  ),

  attributes: {
    content: {
      type: 'string',
      default: __(
        '',
        THEME_NAME
      ),
    },
    tag: {
      type: 'string',
      default: '',
    },
    color: {
      type: 'string',
      default: 'btn-wrap btn-wrap-red',
    },
    size: {
      type: 'string',
      default: '',
    },
  },
  supports: {
    align: [ 'left', 'center', 'right' ],
    inserter: false,
  },

  edit( { attributes, setAttributes } ) {
    const { content, color, size, tag } = attributes;

    // function unescapeHTML(html) {
    //   var escapeEl = document.createElement(textarea);
    //   escapeEl.innerHTML = html;
    //   return escapeEl.textContent;
    // }

    return (
      <Fragment>
        <InspectorControls>
          <PanelBody title={ __( '', THEME_NAME ) }>
            <TextareaControl
              label={ __( '', THEME_NAME ) }
              value={ tag }
              onChange={ ( value ) => setAttributes( { tag: value } ) }
            />

            <SelectControl
              label={ __( '', THEME_NAME ) }
              value={ color }
              onChange={ ( value ) => setAttributes( { color: value } ) }
              options={ [
                {
                  value: 'btn-wrap btn-wrap-red',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-pink',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-purple',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-deep',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-indigo',
                  label: __( '[]', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-blue',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-blue',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-cyan',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-teal',
                  label: __( '[]', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-green',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-light-green',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-lime',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-yellow',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-amber',
                  label: __( '[]', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-orange',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-deep-orange',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-brown',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: 'btn-wrap btn-wrap-grey',
                  label: __( '', THEME_NAME ),
                },
              ] }
            />

            <SelectControl
              label={ __( '', THEME_NAME ) }
              value={ size }
              onChange={ ( value ) => setAttributes( { size: value } ) }
              options={ [
                {
                  value: '',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: ' btn-wrap-m',
                  label: __( '', THEME_NAME ),
                },
                {
                  value: ' btn-wrap-l',
                  label: __( '', THEME_NAME ),
                },
              ] }
            />
          </PanelBody>
        </InspectorControls>
        <span className={ 'button-wrap-msg' }>
          <RichText value={ content } />
        </span>
        <div
          className={ color + size + BUTTON_BLOCK }
          dangerouslySetInnerHTML={ { __html: tag } }
        ></div>
      </Fragment>
    );
  },

  save( { attributes } ) {
    const { content, color, size, tag } = attributes;
    return (
      <div className={ color + size + BUTTON_BLOCK }>
        <RichText.Content value={ tag } />
      </div>
    );
  },
} );
