/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useBlockProps, InspectorControls } from '@wordpress/block-editor';
import {
  SelectControl,
  PanelBody,
  TextControl,
  ToggleControl,
  SearchControl,
  RangeControl,
  __experimentalDivider as Divider,
  Disabled,
} from '@wordpress/components';
import { Fragment, useState } from '@wordpress/element';
import { ServerSideRender } from '@wordpress/editor';
import { useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { THEME_NAME, CreateCategoryList } from '../../helpers';

export default function edit( props ) {
  const { attributes, setAttributes, className } = props;
  const {
    showAllDays,
    days,
    rank,
    pv,
    post_type,
    count,
    type,
    bold,
    arrow,
    showAllCats,
    cats,
    children,
    horizontal,
    ex_posts,
    ex_cats,
  } = attributes;

  const classes = classnames( 'popular-list-box', 'block-box', {
    [ className ]: !! className,
    [ attributes.className ]: !! attributes.className,
  } );
  setAttributes( { classNames: classes } );

  // 
  const [ catSearchInput, setCatSearchInput ] = useState( '' );
  // 
  const [ exCatSearchInput, setExCatSearchInput ] = useState( '' );

  // wp.core
  const categoryData = useSelect( ( select ) => {
    return select( 'core' ).getEntityRecords( 'taxonomy', 'category' );
  } );

  // 
  let catsTextControl = (
    <Fragment>
      <TextControl
        label={ __( '', THEME_NAME ) }
        value={ cats }
        onChange={ ( value ) => setAttributes( { cats: value } ) }
      />
      <PanelBody
        title={ __( '', THEME_NAME ) }
        initialOpen={ true }
      >
        <SearchControl
          value={ catSearchInput }
          onChange={ setCatSearchInput }
        />
        { CreateCategoryList( categoryData, catSearchInput, cats, ( attr ) => {
          setAttributes( { cats: attr } );
        } ) }
      </PanelBody>
      <ToggleControl
        label={ __( '', THEME_NAME ) }
        checked={ children }
        onChange={ ( isChecked ) => setAttributes( { children: isChecked } ) }
      />
    </Fragment>
  );
  if ( showAllCats ) {
    catsTextControl = <Disabled>{ catsTextControl }</Disabled>;
  }

  let exCatsTextControl = (
    <Fragment>
      <TextControl
        label={ __( '', THEME_NAME ) }
        value={ ex_cats }
        onChange={ ( value ) => setAttributes( { ex_cats: value } ) }
      />
      <PanelBody
        title={ __( '', THEME_NAME ) }
        initialOpen={ true }
      >
        <SearchControl
          value={ exCatSearchInput }
          onChange={ setExCatSearchInput }
        />
        { CreateCategoryList(
          categoryData,
          exCatSearchInput,
          ex_cats,
          ( attr ) => {
            setAttributes( { ex_cats: attr } );
          }
        ) }
      </PanelBody>
    </Fragment>
  );
  if ( showAllCats == false ) {
    exCatsTextControl = <Disabled>{ exCatsTextControl }</Disabled>;
  }

  let daysNumberControl = (
    <SelectControl
      label={ __( '', THEME_NAME ) }
      value={ days }
      onChange={ ( newValue ) =>
        setAttributes( { days: parseInt( newValue ) } )
      }
      options={ [
        {
          label: __( '', THEME_NAME ),
          value: 0,
        },
        {
          label: __( '1', THEME_NAME ),
          value: 1,
        },
        {
          label: __( '3', THEME_NAME ),
          value: 3,
        },
        {
          label: __( '1', THEME_NAME ),
          value: 7,
        },
        {
          label: __( '2', THEME_NAME ),
          value: 14,
        },
        {
          label: __( '1', THEME_NAME ),
          value: 30,
        },
        {
          label: __( '2', THEME_NAME ),
          value: 60,
        },
        {
          label: __( '3', THEME_NAME ),
          value: 90,
        },
        {
          label: __( '', THEME_NAME ),
          value: 182,
        },
        {
          label: __( '1', THEME_NAME ),
          value: 365,
        },
      ] }
    />
  );
  if ( showAllDays ) {
    daysNumberControl = <Disabled>{ daysNumberControl }</Disabled>;
  }

  const getPopularListContent = () => {
    return <ServerSideRender block={ props.name } attributes={ attributes } />;
  };

  return (
    <Fragment>
      <InspectorControls>
        <PanelBody title={ __( '', THEME_NAME ) } initialOpen={ true }>
          <RangeControl
            label={ __( '', THEME_NAME ) }
            isShiftStepEnabled={ false }
            value={ count }
            onChange={ ( newValue ) => setAttributes( { count: newValue } ) }
            min={ 1 }
          />
          <ToggleControl
            label={ __( '', THEME_NAME ) }
            checked={ showAllDays }
            onChange={ ( isChecked ) => {
              setAttributes( { showAllDays: isChecked } );
            } }
          />
          { daysNumberControl }
          <SelectControl
            label={ __( '', THEME_NAME ) }
            value={ type }
            onChange={ ( newType ) => setAttributes( { type: newType } ) }
            options={ [
              {
                label: __( '', THEME_NAME ),
                value: 'default',
              },
              {
                label: __( '', THEME_NAME ) + __( '', THEME_NAME ),
                value: 'border_partition',
              },
              {
                label: __( '', THEME_NAME ),
                value: 'border_square',
              },
              {
                label: __( '', THEME_NAME ),
                value: 'large_thumb',
              },
              {
                label: __( '', THEME_NAME ),
                value: 'large_thumb_on',
              },
            ] }
          />
          <Divider />
          <ToggleControl
            label={ __( '', THEME_NAME ) }
            checked={ bold }
            onChange={ ( isChecked ) => setAttributes( { bold: isChecked } ) }
          />
          <ToggleControl
            label={ __( '', THEME_NAME ) }
            checked={ arrow }
            onChange={ ( isChecked ) => setAttributes( { arrow: isChecked } ) }
          />
          <ToggleControl
            label={ __( '', THEME_NAME ) }
            checked={ horizontal }
            onChange={ ( isChecked ) =>
              setAttributes( { horizontal: isChecked } )
            }
          />
          <ToggleControl
            label={ __( '', THEME_NAME ) }
            checked={ rank }
            onChange={ ( isChecked ) => {
              setAttributes( { rank: isChecked } );
            } }
          />
          <ToggleControl
            label={ __( 'PV', THEME_NAME ) }
            checked={ pv }
            onChange={ ( isChecked ) => {
              setAttributes( { pv: isChecked } );
            } }
          />
        </PanelBody>
        <PanelBody title={ __( '', THEME_NAME ) } initialOpen={ false }>
          <ToggleControl
            label={ __( '', THEME_NAME ) }
            checked={ showAllCats }
            onChange={ ( isChecked ) => {
              setAttributes( { showAllCats: isChecked } );
              // 
              setCatSearchInput( '' );
            } }
          />
          { catsTextControl }
          { exCatsTextControl }
          <Divider />
          <TextControl
            label={ __( '', THEME_NAME ) }
            value={ post_type }
            onChange={ ( newValue ) =>
              setAttributes( { post_type: newValue } )
            }
          />
          <TextControl
            label={ __( '(ID)', THEME_NAME ) }
            value={ ex_posts }
            onChange={ ( newValue ) => setAttributes( { ex_posts: newValue } ) }
          />
        </PanelBody>
      </InspectorControls>
      <div { ...useBlockProps() }>{ getPopularListContent() }</div>
    </Fragment>
  );
}
