import { THEME_NAME } from '../../helpers';
import { __ } from '@wordpress/i18n';
import {
  RichText,
  InspectorControls,
  useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, SelectControl } from '@wordpress/components';
import { Fragment } from '@wordpress/element';
import classnames from 'classnames';

export default function edit( { attributes, setAttributes, className } ) {
  const { content, style } = attributes;

  function onChangeContent( newContent ) {
    setAttributes( { content: newContent } );
  }

  const classes = classnames( style, className );
  const blockProps = useBlockProps( {
    className: classes,
  } );

  return (
    <Fragment>
      <InspectorControls>
        <PanelBody title={ __( '', THEME_NAME ) }>
          <SelectControl
            label={ __( '', THEME_NAME ) }
            value={ style }
            onChange={ ( value ) => setAttributes( { style: value } ) }
            options={ [
              {
                value: 'blogcard-type bct-none',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-related',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-reference',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-reference-link',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-popular',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-together',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-detail',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-check',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-pickup',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-official',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-dl',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-prev',
                label: __( '', THEME_NAME ),
              },
              {
                value: 'blogcard-type bct-next',
                label: __( '', THEME_NAME ),
              },
            ] }
          />
        </PanelBody>
      </InspectorControls>

      <div { ...blockProps }>
        <RichText
          onChange={ onChangeContent }
          value={ content
            .replace( /^\s+/, '' )
            .replace( /\s+$/, '' )
          }
          // multiline="p"
        />
      </div>
    </Fragment>
  );
}
